@include tui('.card-header-tabs') {
  margin-right: -($card-spacer-x / 2);
  margin-bottom: -$card-spacer-y;
  margin-left: -($card-spacer-x / 2);
  border-bottom: 0;
}


@include tui('.card-header-pills') {
  margin-right: -($card-spacer-x / 2);
  margin-left: -($card-spacer-x / 2);
}

@include tui('.card-img-overlay-container') {
  position: relative;
}


@include tui('.card-img-overlay') {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: $card-img-overlay-padding;
}

@include tui('.card-img') {
  width: 100%; // Required because we use flexbox and this inherently applies align-self: stretch
}

